import { Component } from '@angular/core';
import { IonicPage, ViewController, AlertController } from 'ionic-angular';
import { JhiEventManager } from 'ng-jhipster';
import * as providers from '../../providers';

@IonicPage({
  name: 'LoginPage',
  segment: 'login'
})
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  account: any;

  constructor(
    private eventManager: JhiEventManager,
    private viewCtrl: ViewController,
    public alertCtrl: AlertController,
    private loading: providers.AppLoadingProvider,
    private loginProvider: providers.LoginProvider
  ) {
    this.account = {};
  }

  doLogin() {

    this.loading.start(() => {
      this.account.rememberMe = true;

      return this.loginProvider.login(this.account).then(() => {
        this.viewCtrl.dismiss('login');
  
        this.eventManager.broadcast({
          name: 'authenticationSuccess',
          content: 'Sending Authentication Success'
        });
  
      }).catch(() => {
        let alert = this.alertCtrl.create({
          title: 'エラー',
          subTitle: 'ログインに失敗しました。入力をお確かめの上、再度実行してください。',
          buttons: ['OK']
        });
        alert.present();
      });
    });
  }

  requestResetPassword() {
    this.viewCtrl.dismiss('resetPassword');
  }

  dismiss(data?: any) {
    this.viewCtrl.dismiss('cancel');
  }
}



